<template>
    <el-main>
      <el-row>
        <el-col :span="24">

          <el-button style="float:left"  type="primary" icon="el-icon-refresh" @click="init()"></el-button>
          <div class="demonstration h_title">{{getlan('DataBase')}}</div>
           <el-button  type="primary" icon="el-icon-plus" @click="adddb()"></el-button>
        </el-col>
      </el-row>

      <el-table :data="tableData"  height="500"   :row-style="{height:'40px'}"  
    :cell-style="{padding:'0'}"
    style="font-size: 15px">
       
        <el-table-column :label="getlan('DataBase')" >
          <template slot-scope="scope">
           
            <router-link  style="float:left;margin-left:5px"  :to="{name:'tables', params: {dbname: scope.row.dbname }}"> 
               <span style="margin-left: 10px">{{ scope.row.dbname }}</span>
            </router-link>
          </template>
        </el-table-column>
        
        <el-table-column :label="getlan('operate')" width="260">
          <template slot-scope="scope">
             <el-button size="small"   icon="el-icon-document" @click="showtable(scope.$index, scope.row)"></el-button>
             <el-button size="small"   icon="el-icon-edit" @click="handleData(scope.$index, scope.row)"></el-button>
             <el-button size="small" icon="el-icon-delete"  @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

<div class="block">
            <span class="demonstration"></span>
            <el-pagination 
                layout="prev, next" 
                @size-change="handleSizeChange"
                @current-change="current_change"
                :page-sizes="[50, 100, 500]"
                :page-size="this.pagesize"
                :total="this.total">
            </el-pagination>
        </div>

     <el-dialog :title="getlan('DataBase')" :visible.sync="dialogServer"  :width="this.getDialogWidth()"   :show-close="true" :close-on-click-modal="false" :close-on-press-escape="false">
        <el-form :model="form" :rules="rules" ref="form" class="el-form--label-top">
          <el-form-item label="Sql" prop="sqlString">
           	<el-input type="textarea" :autosize="{ minRows: 10}" placeholder="sqlString" v-model="form.sqlString"></el-input>
          </el-form-item>
      
        </el-form>
        <span slot="footer" class="dialog-footer">
       <span style="color:red">{{this.errorMessage}}</span>
                <el-button @click="handleClose('form')">{{getlan('cancel')}}</el-button>
                <el-button type="primary" @click="submitForm('form')">{{getlan('Execute')}}</el-button>
            </span>
      </el-dialog>

    </el-main>
</template>

<script>
import Qs from 'qs'
    import{Type,AddData,DeleteData,GetList} from "@/api/index"
     import{getDay,getDateTime,getReqUrl,getLang,getValue,setValue} from "@/utils/pub"
    export default {
        name: 'App',
        data() {
        
            return {
              pageid: 1,
            pagesize: 50,
              loadFinish:0,
               tableData:[],
              CategoryName:'',
            errorMessage:'',
            total:0,
             dialogServer:false,
              belong:this.$route.params.belong,
              form:{
                  sqlString:'',
                table:'runsql',
             
              },
               querymap:{ 
               pageid:this.pageid,
                pagesize:this.pagesize,
                table:'database',
              },
              rules:{
               DataBasename:[
                  { required: true, message: 'Please input name', trigger: 'change' },
                  { min: 1, max: 30, message: 'length 1 - 30', trigger: 'change' }
                ],
              }
            }
        },
        created(){
          this.belong=this.$route.params.belong;
          this.init();
          this.form.sqlString ="CREATE DATABASE dbname;"
        },
         methods: {
          getlan(name){ var str=this.$t('lang.'+name+''); if(str.indexOf("lang.")>=0){ return name; }return str },
          init(){

            GetList(Qs.stringify(this.querymap)).then(res=>{
              this.tableData = res.data.data;
              if(this.tableData!=undefined){
                this.total=this.tableData.length;
              }
            })

          },
           goback(){
	           this.$router.push({name:'base'});
          },
            showtable(index, row){
               this.$router.push({name: 'tables', params: {dbname: row.dbname}})
            },
               adddb(){
                 this.form.sqlString="CREATE DATABASE xx";
              this.dialogServer = true;
          },
          handleData(index, row){
            this.errorMessage="";
              this.form = row;
              this.form.mode='edit'
              this.dialogServer = true;
            
          },
           handleDelete(index, row) {
              this.form = row;
              this.form.mode='edit'
              this.dialogServer = true;

        
          },
          handleClose(formName){
            this.dialogServer = false;
            this.form = {
              id:0,
                DataBasename:'',

            }
            this.$refs['form'].resetFields();
          },//分页
        current_change:function(pageid){
            this.pageid = pageid;
        },
        handleSizeChange(val) {
            this.pagesize = val;
            
        },
         
         
         
          submitForm(formName) {
          
          if(this.form.sqlString==''){
             this.alert_ok('sql is null')
             return


            }
             
          var  params={ 
              sql:this.form.sqlString,
              table:'runsql'
              }
             // console.log("this.sql="+this.form.sqlString)
            AddData(Qs.stringify(params)).then(res=>{
             
              if(res.data.code==1){
                this.dialogServer = false;
               // this.$message({message:res.data.message,type: 'success'});
              }else{
                   if(res.data.message!=null){
                     this.errorMessage=res.data.message
                     this.alert_ok(res.data.message)
                   }
              }
            })

          }
        }
    }
</script>

<style lang="scss" scoped>
  .el-main {
    margin: 0px 0 0 160px;
    padding: 10px;
    .el-col {
      text-align: right;
    }
    .el-form--label-top .el-form-item__label {
      padding: 0;
    }
    .el-button.add {
      background-color: #4caf50;
      border: 0;
      font-size: 12px;
    }
     .h_title{
      margin-left:20px;
      float: left;
      line-height: 40px;
      font{
        color: #4caf50;
      }
    }
  }
</style>



